<template>
    <div id="order">
        <Header :Flagadd="false"  :FlagHome="false" :FlagCity="false"  :FlagSignin="false"  :FlagSearch="false"   :FlagOrder="true"  :FlagAcc="false"></Header>
        <div class="content">
            <div class="co">
                <div class="co1">
                    123
                </div>
                <div class="co2">
                    <div class="con1">
                        <div class="con1-1">
                            <span>效果演示<van-icon name="arrow" class="van" /></span>
                            <span>等待支付</span>
                        </div>
                        <div class="con1-2">2019-09-08  18:39</div>
                    </div>
                    <div class="con2">
                        <span>的味道切尔奇二群翁</span>
                        <span>￥855.00</span>
                    </div>
                    <div class="con3">
                      <span>去支付(还剩14分50秒)</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mspState, mapGetters, mapActions, mapMutations } from "vuex";
import Header from "../components/Header";
export default {
    components: {
      Header
    },
    name: "Order",
    data() {
      return {
        msg: "Welcome to Your Vue.js App"
      };
    },
    created() {},
    methods: {},
    mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#order {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .content{
      width: 100%;
      margin-top: 1.6rem;
      .co{
        width: 100%;
        margin-top: 0.4rem;
        display: flex;
        background: white;
        .co1{
          width: 15%;
          border: 0.01rem solid black;
          margin-top: 0.3rem;
        }
        .co2{
          margin-top: 0.3rem;
          width: 77%;
          margin-left: 4%;
          .con1{
            width: 100%;
            border-bottom:0.01rem solid gainsboro;
            line-height: 1rem; 
            .con1-1{
              width: 100%;
              justify-content: space-between; 
              display: flex;
              
              span{
                display: block;
                .van{
                  font-size: 0.4rem;
                  color: gray;
                }
              }
            }
            .con1-2{
              font-size: 0.5rem;
              color: gray;
            }
          }
          .con2{
            width: 100%;
            justify-content: space-between; 
            display: flex;
            border-bottom:0.01rem solid gainsboro;
            line-height: 2rem; 
            span{
              display: block;
            }
            span:nth-of-type(2){
              color: red;
            }
          }
          .con3{
            height: 2rem;
            color: #F4AE4F;
            position: relative;
            span{
              display: block;
              width: 6rem;
              border: 0.05rem solid #F4AE4F;
              border-radius: 3px;
              font-size: 0.55rem;
              text-align: center;
              line-height: 0.8rem;
              position: absolute;
              top:0.7rem;
              right: 0;
            }
          }
        }
      }
    }
}
</style>



